import React, { Component } from 'react';
import ChartPage from '../../../components/ChartPage';
import * as d3 from 'd3';

export default class Page extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: this.props.text,
      dataSet: {
        data: [],
        xmin: 0,
        xmax: 100,
      },
    }
  }

  dataSet = () => {
    const r = [];
    const l = this.state.text.split('\n')
    let xmin = null;
    let xmax = null;
    for (var i in l) {
      const t = l[i].replace(/(\s+)/g, ' ').split(' ');
      if (t.length >= 2) {
        if (xmin === null || xmin > t[1]) xmin = Number(t[1]);
        if (xmax === null || xmax < t[1]) xmax = Number(t[1]);
        r.push({
          label: t[0],
          x: Number(t[1]),
        })
      }
    }
    xmin = 0;
    xmax += 10;
    return { data: r, xmin, xmax, }; 
  }

  initialize = (svg, { width, height }) => {
    const { xmin, xmax, } = this.state.dataSet;

    this.xScale = d3.scaleLinear()
      .domain([xmin, xmax])
      .range([0, width]);
    this.yScale = d3.scaleLinear()
      .domain([0, height/16])
      .range([0, height]);

    this.xAxis = d3.axisBottom(this.xScale)
      .tickSize(height)
      .tickFormat(d => d)

    this.setState({
      dataSet: this.dataSet(),
    })
  }

  update = (svg, { width, height }) => {
    const self = this;
    const { xmin, xmax, } = this.state.dataSet;

    this.xScale
      .domain([xmin, xmax])
      .range([0, width]);
    this.yScale
      .domain([0, height/16])
      .range([0, height]);

    this.xAxis
      .tickSize(height)
      .tickFormat(d => d)

    const g = svg.select('g')

    g.selectAll('rect')
     .data(this.state.dataSet.data)
     .exit()
     .remove()

    g.selectAll('rect')
     .data(this.state.dataSet.data)
     .enter()
     .append('rect')
     .attr('x', (d,i) => 0)
     .attr('y', (d,i) => this.yScale(i))
     .attr('width', (d,i) => this.xScale(d.x))
     .attr('height', (d,i) => 14)

    g.selectAll('rect')
     .attr('x', (d,i) => 0)
     .attr('y', (d,i) => this.yScale(i))
     .attr('width', (d,i) => this.xScale(d.x))
     .attr('height', (d,i) => 14)
     .attr("fill", "#ACD8FF");

    g.selectAll('text.label')
     .data(this.state.dataSet.data)
     .exit()
     .remove()

    g.selectAll('text.label')
     .data(this.state.dataSet.data)
     .enter()
     .append('text')
     .classed('label', true)
     .attr('x', d => 2)
     .attr('y', (d,i) => this.yScale(i))
     .text(d => d.label)
     .attr("text-anchor", "left")
     .attr("alignment-baseline", "hanging")
     .style('pointer-events', 'none')

    g.selectAll('text.label')
     .attr('x', d => 2)
     .attr('y', (d,i) => this.yScale(i))
     .text(d => d.label)
     .attr("fill", "gray");

    g.select('g.xAxis').remove()

    function customXAxis(g) {
      g.call(self.xAxis);
      g.select('.domain').remove();
      g.selectAll(".tick line").attr("stroke", "#777").attr("stroke-dasharray", "2,2");
      g.selectAll(".tick text").attr('text-anchor','start').attr('transform',`rotate(${-90},${10},${height-15})`).attr('x',10).attr("y", height-15).attr("dx", -8);
    }

    g.append("g")
      .classed('xAxis', true)
      .call(customXAxis);

  }

  onChangeText = (text) => {
    this.setState({ text, }, () => {
      this.setState({ dataSet: this.dataSet() }, () => {
      })
    });
  }

  render() {
    return (
      <ChartPage
        title={this.props.title}
        initialize={this.initialize}
        update={this.update}
        onChangeText={this.onChangeText}
        text={this.state.text}
      />
    )
  }
}

Page.defaultProps = {
  title: 'バーチャート',
  text: `A\t150
B\t100
C\t80
D\t70
E\t40
F\t10
G\t0
`,
}
